.product {
  padding: 105px 0 120px;
  text-align: center;

  &__items {
    display: grid;
    grid-gap: 45px 30px;
    grid-template-columns: repeat(3, 1fr);
  }

  &__link {
    display: inline-block;
    margin-top: 55px;
    padding: 14px 27px;
    border: 1px solid $accent;
    text-transform: uppercase;
    color: $title;
  }
}

.product-item {
  max-width: 370px;
  text-align: center;

  &__img-box {
    position: relative;
    margin-bottom: 20px;
    height: 370px;
    overflow: hidden;
  }

  &__link-box {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0 auto;
    margin-bottom: 25px;
    width: 220px;
    height: 50px;
    border-radius: 22px;
    box-shadow: 0 5px 30px 0 rgba(152, 152, 152, 0.2);
    background-color: $wh;
    transform: translateY(300%);
    transition: all 0.3s;
  }

  &__link {
    display: inherit;
    width: 20px;
    height: 20px;
    fill: #29282d;
    transition: all 0.3s;

    &:hover {
      fill: $accent;
    }
  }

  &__link--line::before {
    left: 70px;
  }

  &__link--line::after {
    right: 70px;
  }

  &__images {
    object-fit: cover;
  }

  &__title {
    padding: 15px 0 20px;
    font-size: 22px;
    color: $title;
  }

  &__price {
    display: flex;
    justify-content: center;

    @extend %roboto-400;
  }

  &__new-price {
    padding: 0 12px;
    color: $title;
  }

  &__old-price {
    padding: 0 12px;
    text-decoration: line-through;
  }

  &__content-box {
    display: none;
  }

  &:hover {
    .product-item__link-box {
      transform: translateY(0);
    }
  }

  &.product-item--list {
    display: flex;
    min-width: 100%;
    text-align: left;

    .product-item__img-box {
      margin-bottom: 0;
      height: 250px;
      min-width: 200px;

      img {
        height: 250px;
      }
    }

    .product-item__link-box {
      bottom: auto;
      width: 115px;
      box-shadow: none;
      background-color: transparent;

      .product-item__link {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: $wh;

        .product-item__pic {
          width: 20px;
          height: 20px;
        }
      }

      .product-item__link--line {
        display: none;
      }
    }

    .product-item__content-box {
      display: block;
    }

    .product-item__wrapper-box {
      padding: 28px 40px;
      border: 1px solid #ececec;
      border-left: none;
    }

    .product-item__box {
      display: flex;
      flex-wrap: wrap;
    }

    .product-item__title {
      order: -1;
      margin-bottom: 10px;
      padding: 0;
      width: 100%;
    }

    .product-item__price {
      order: -1;
      margin-left: -12px;
    }

    .star {
      margin-left: 23px;
    }

    .product-item__text {
      margin-bottom: 25px;
    }

    .product-item__btn {
      padding: 12px 40px;
      border: none;
      color: $wh;
      background-color: $title;
      cursor: pointer;

      &:hover {
        box-shadow: 0 7px 20px 0 rgba(255, 17, 48, 0.3);
        background-color: $accent;
      }
    }

    &:hover {
      .product-item__link-box {
        transform: translateY(-280%);
      }
    }
  }

  &--sale {
    .product-item__img-box::before {
      content: "SALE";
      position: absolute;
      left: 25px;
      top: 25px;
      line-height: 1;

      @extend %btn;
    }
  }

  &__link--line::before,
  &__link--line::after {
    content: "";
    position: absolute;
    top: 10px;
    width: 1px;
    height: 30px;
    background-color: #ececec;
  }
}

.star {
  margin: 0 auto;
}