$accent: #fe3e57;
$default: #8d8d8d;
$title: #29282d;
$bg: #f6f6f6;
$wh: #fff;

%quicksand-500 {
  font-family: "Quicksand-Medium", sans-serif;
  font-weight: 500;
}

%quicksand-400 {
  font-family: "Quicksand-Regular", sans-serif;
  font-weight: 400;
}

%roboto-400 {
  font-family: "Roboto-Regular", sans-serif;
  font-weight: 400;
}

%roboto-700 {
  font-family: "Roboto-Bold", sans-serif;
  font-weight: 700;
}

%btn {
  padding: 12px 26px;
  box-shadow: 0 7px 20px 0 rgba(255, 17, 48, 0.3);
  text-transform: uppercase;
  color: $wh;
  background-color: $accent;
  cursor: pointer;

  @extend %roboto-400;
}

%checkbox-hidden,
%radio-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
